@import url(https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&display=swap);
*{
    box-sizing: border-box;
}

html{
    font-family: 'Times New Roman', Times, serif;
}

body {
    margin: 0;
    background-color: rgb(74, 81, 187);
}

.new-expense{
    background-color: #000000;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
}

.new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: rgb(255, 255, 255);
    border-radius: 12px;
    margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active{
    background-color: #432e9f;
    border-color: #510674;
}

.new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
    background-color: #8450a3;
}

.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    color: rgb(247, 5, 5);
}

.new-expense__controls label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}
.new-expense__controls input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid rgb(0, 0, 0);
    width: 20rem;
    max-width: 100%;
}

.new-expense__actions {
     text-align: right;
}
.expense-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #960000;
}
.expense-item h2 {
    color: #5e5e5e;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: rgb(236, 207, 207);
}
.expense-item__description{
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex:1 1;
}
.expense-item__price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
}
@media (min-width:580px) {
    .expense-item__description{
        flex-direction:row;
        align-items:center;
        justify-content:flex-start;
        flex:1 1;
    }
    .expense-item__description h2 {
        font-size:1.25rem;
    }

    .expense-item__price {
        font-size: 1.25rem;
        padding:0.5rem 1.5rem;

    }
    
}
.expense-date{
    display: flex;
    flex-direction: column;
    width: 5.5em;;
    height: 5.5em;
    border: 1px solid #ececec;
    background-color: #2a2a2a; 
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}

.expense-date__month{
    font-size: 0.75rem;
    font-weight: bold;
}
.expense-date__year{
    font-size: 0.75rem;
}
.expense-date__day{
    font: size 1.5rem;
    font-weight: bold;
}
.expenses{
    padding: 1rem;
    background-color: rgb(31, 31,31);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    box-shadow: 0 1px  8px rgba(0, 0, 0, 0.25);
}
